    changeSubDescPadding() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return 'px-5 py-1';
        case 'sm':
          return 'px-7 py-2';
        case 'md':
          return 'px-9 py-3';
        case 'lg':
          return 'px-11 py-4';
        default:
          return 'px-11 py-4';
      }
    },

    quoteHeight() {
      return 350;
