<script>
export default {
  computed: {
    titleClass() {
      const base = 'text-left font-weight-bold pt-0 mb-1 grey--text text--darken-2';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return `${base} caption`;
        case 'sm': return `${base} caption`;
        case 'md': return `${base} body-2`;
        default: return `${base} body-1`;
      }
    },

    subClass() {
      const base = 'text-left font-weight-medium mb-1 grey--text text--darken-2';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return `${base} caption`;
        case 'sm': return `${base} caption`;
        case 'md': return `${base} body-2`;
        default: return `${base} body-1`;
      }
    },

    small() {
      return this.$vuetify.breakpoint.xsOnly;
    },
  },
};
</script>
